import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { OrderStateCategoryEnum } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { KitchenOrderBox } from '../molecules/KitchenOrderBox'
import { useKitchenOrderList } from './KitchenOrderListLoader'

interface IProps {
  category: OrderStateCategoryEnum
}

const InfoMessage = styled.div`
  margin: 0 0.5rem;
`

export const KitchenOrderList = observer<IProps>(({ category }) => {
  const { order: orderModel } = useRoot()
  const { loading, noFilter, orders } = useKitchenOrderList()

  if (noFilter) {
    return (
      <InfoMessage>
        <Trans>Select a branch</Trans>
      </InfoMessage>
    )
  }

  if (loading) {
    return (
      <InfoMessage>
        <Trans>Loading</Trans>
      </InfoMessage>
    )
  }

  const categoryOrders = orders.filter(
    RM.propEq('orderStateCategory', category),
  )

  if (categoryOrders.length === 0) {
    switch (category) {
      case OrderStateCategoryEnum.NewOrders:
        return (
          <InfoMessage>
            <Trans>No new orders</Trans>
          </InfoMessage>
        )
      case OrderStateCategoryEnum.Preparing:
        return (
          <InfoMessage>
            <Trans>No orders to prepare</Trans>
          </InfoMessage>
        )
      case OrderStateCategoryEnum.ReadyToDeliver:
        return (
          <InfoMessage>
            <Trans>No orders to deliver</Trans>
          </InfoMessage>
        )
      default:
        return (
          <InfoMessage>
            <Trans>No orders</Trans>
          </InfoMessage>
        )
    }
  }

  return (
    <>
      {categoryOrders.map(order => (
        <KitchenOrderBox
          key={order.id}
          order={order}
          showBranch={orderModel.list.branchesFilter.length > 1}
        />
      ))}
    </>
  )
})
