import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'
import { Colors } from '../../../styling'

interface IOrderListBranchFilterProps {}

export const OrderListBranchFilter: React.FC<
  IOrderListBranchFilterProps
> = () => {
  const { order } = useRoot()
  return useObserver(() => (
    <CompanyBranchPicker
      selectedBranches={Array.from(order.list.branches)}
      onSelectionChange={branches => {
        order.list.setBranchFilter(branches)
      }}
      onSelectionClear={order.list.clearBranchFilter}
      textColor={Colors.turquoise}
      isMulti={true}
      isClearable={false}
    />
  ))
}
