import React from 'react'

import { formatPrice } from '../helpers/formats'

interface IPriceProps {
  price: number
  currencyCode?: string
  className?: string
}

export const Price: React.FC<IPriceProps> = ({
  price,
  currencyCode,
  className,
}) => (
  <span className={className}>
    {currencyCode
      ? `${formatPrice(price)} ${currencyCode}`
      : formatPrice(price)}
  </span>
)
