import React from 'react'

import { OrderStateEnum } from '../../../graph/generated'
import { GUpdateOrderState } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { UpdateOrderStateMutation } from '../../order/models/OrderStateModel.graphql'

interface IProps {
  orderId: ID
}

export const KitchenAcceptButton: React.FC<IProps> = ({ orderId }) => {
  const acceptOrder = useGuardedMutation<
    GUpdateOrderState.Mutation,
    GUpdateOrderState.Variables
  >(UpdateOrderStateMutation)

  return (
    <TouchButton
      iconName="keyboard_arrow_right"
      width={Spacing.ButtonHeight}
      color={Colors.green}
      onClick={() => {
        acceptOrder({ orderId, state: OrderStateEnum.InProduce })
      }}
    />
  )
}
