import { MutationFn, MutationHookOptions } from '@speedlo/graphql'
import { isApolloError } from 'apollo-client/errors/ApolloError'
import { GraphQLError } from 'graphql'
import React from 'react'

import { ShowFeedbackButton } from '../atoms/ShowFeedbackButton'
import { RM } from '../helpers/ramda'
import { reportError } from '../helpers/sentry'
import { i18n } from '../i18n'
import { useSnackBar } from './useSnackBar'

interface IMutationGuardOptions<TData> {
  getUserErrors?(data: TData): ReadonlyArray<string> | null
}

export function useMutationGuard<TData, TVariables>(
  mutate: MutationFn<TData, TVariables>,
  options: IMutationGuardOptions<TData> = {},
) {
  const defaultMessage = i18n.t`Unexpected error occured while sending a request. Our team was notified about the issue.`

  const { enqueueSnackbar, enqueueError, enqueueWarning } = useSnackBar()

  const handleUserErrors = (userErrors: ReadonlyArray<string>) => {
    userErrors.forEach(enqueueWarning)
  }

  const handleGraphQLErrors = (errors: ReadonlyArray<GraphQLError>) => {
    const userErrors = errors.filter(
      RM.pathEq(['extensions', 'showToUser'], true),
    )
    if (userErrors.length === 0) {
      enqueueError(defaultMessage)
    } else {
      handleUserErrors(userErrors.map(RM.prop('message')))
    }
  }

  const handleError = (err: Error) => {
    if (isApolloError(err)) {
      if (err.networkError) {
        enqueueError(
          i18n.t`Communication with server has failed. Please retry the operation in a moment again.`,
        )
        return
      } else if (err.graphQLErrors) {
        handleGraphQLErrors(err.graphQLErrors)
        return
      }
    }
    reportError(err)
    enqueueSnackbar(defaultMessage, {
      action: [React.createElement(ShowFeedbackButton, { key: 'feedback' })],
    })
  }

  return async (
    variables: TVariables,
    mutateOptions: MutationHookOptions<TData, TVariables> = {},
  ) => {
    try {
      const result = await mutate(variables, mutateOptions)
      if (result.errors) {
        handleGraphQLErrors(result.errors)
        return null
      }
      if (!result.data) {
        return null
      }
      if (options.getUserErrors) {
        const userErrors = options.getUserErrors(result.data)
        if (userErrors !== null) {
          handleUserErrors(userErrors)
        }
      }
      return result.data
    } catch (err) {
      handleError(err)
      return null
    }
  }
}
