import React from 'react'
import styled from 'styled-components'

import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { IconLine } from '../../../atoms/IconLine'
import { i18n } from '../../../i18n'
import { Colors } from '../../../styling'

interface INoteLineProps {
  note?: string | null
  previousNote?: string | null
}

const CurrentNoteStyled = styled.span`
  color: ${Colors.orange};
`
const PreviousNoteStyled = styled.span`
  color: ${Colors.greyDark};
  margin-left: 0.5rem;
`

export const NoteLine: React.FC<INoteLineProps> = ({ note, previousNote }) =>
  note || previousNote ? (
    <>
      <HorizontalSeparator />
      <IconLine icon="note" labelMargin="0">
        <CurrentNoteStyled>
          {note || i18n.t`Without note`.toLocaleLowerCase()}
        </CurrentNoteStyled>
        <PreviousNoteStyled>{previousNote}</PreviousNoteStyled>
      </IconLine>
    </>
  ) : null
