import React, { Fragment } from 'react'

import { OrderRecipeStateEnum } from '../../../graph/generated'
import { FKitchenOrder } from '../../../graph/types'
import { KitchenOrderWareLine } from '../molecules/KitchenOrderWareLine'

interface IProps {
  orderWares: ReadonlyArray<FKitchenOrder.Items>
  isCheckable: boolean
}

export const KitchenOrderWareList: React.FC<IProps> = ({
  orderWares,
  isCheckable,
}) => {
  const renderWare = (orderWare: FKitchenOrder.Items) => (
    <Fragment key={orderWare.id}>
      <KitchenOrderWareLine
        recipeId={orderWare.id}
        code={orderWare.recipe.code}
        label={orderWare.recipe.nameLabel}
        isDone={orderWare.orderRecipeState.enum === OrderRecipeStateEnum.Cooked}
        isCheckable={isCheckable}
      />
      <>
        {orderWare.sidedishes.map(sidedish => (
          <KitchenOrderWareLine
            key={`${orderWare.id}-${sidedish.id}`}
            recipeId={sidedish.id}
            code={`+${sidedish.recipe.code}`}
            label={sidedish.recipe.nameLabel}
            isDone={false}
            isCheckable={false}
          />
        ))}
      </>
    </Fragment>
  )
  return <div>{orderWares.map(renderWare)}</div>
}
