import styled from 'styled-components'

interface IProps {
  position: 'TopRight' | 'BottomRight' | 'BottomLeft' | 'TopLeft'
}

const xPositionProperty = ({ position = 'BottomRight' }: IProps) => {
  return position === 'BottomRight' || position === 'BottomLeft'
    ? 'bottom: 0'
    : 'top: 0'
}

const yPositionProperty = ({ position = 'BottomRight' }: IProps) => {
  return position === 'BottomLeft' || position === 'TopLeft'
    ? 'left: 0'
    : 'right: 0'
}

export const CardDock = styled('div')`
  position: absolute;
  ${yPositionProperty};
  ${xPositionProperty};
`
