import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  OrderStateCategoryEnum,
  QKitchenOrderListOrders,
  QKitchenOrderListQuery,
  SKitchenOrderListSubscription,
  useQKitchenOrderList,
  useSKitchenOrderList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'

const requiredCategories = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.ReadyToDeliver,
]

type TOrder = QKitchenOrderListOrders

interface ILoaderContext {
  loading: boolean
  noFilter: boolean
  orders: ReadonlyArray<TOrder>
}

const loaderContext = React.createContext<ILoaderContext>({
  loading: true,
  noFilter: true,
  orders: [],
})

export const KitchenOrderListLoader: React.FC = observer(props => {
  const { order: orderModel } = useRoot()
  const noFilter = !orderModel.list.hasBranchFilter
  const { data, loading, updateQuery, refetch } = useQKitchenOrderList(
    {
      branches: orderModel.list.branchesFilter,
      categories: requiredCategories,
    },
    { skip: noFilter },
  )

  useOnConnectionRestored(refetch)

  useSKitchenOrderList(
    {
      branches: orderModel.list.branchesFilter,
      categories: requiredCategories,
    },
    {
      skip: noFilter,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => handleSubscriptionUpdate(dataUpdate, prev))
        }
      },
    },
  )

  return (
    <loaderContext.Provider
      value={{
        loading,
        noFilter,
        orders: loading ? [] : data.orders,
      }}
    >
      {props.children}
    </loaderContext.Provider>
  )
})

export function useKitchenOrderList() {
  return React.useContext(loaderContext)
}

function handleSubscriptionUpdate(
  update: SKitchenOrderListSubscription,
  prev: QKitchenOrderListQuery,
) {
  const { order } = update.updatedOrder
  if (!order) {
    return prev
  }
  if (!requiredCategories.includes(order.orderStateCategory)) {
    return { orders: prev.orders.filter(o => o.id !== order.id) }
  }
  let orderExists = false
  const orders = prev.orders.map(o => {
    if (o.id === order.id) {
      orderExists = true
      return order
    }
    return o
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { orders }
}
