import React from 'react'
import styled from 'styled-components'

import {
  OrderRecipeStateEnum,
  useMKitchenRecipeState,
} from '../../../graph/generated'
import { Colors, FontSizes } from '../../../styling'
import { TouchCheckButton } from '../atoms/TouchCheckButton'

interface IProps {
  recipeId?: ID
  code?: string
  label: string
  isDone: boolean
  isCheckable: boolean
}

const OrderWareLineWrap = styled.div`
  display: flex;
  justify-content: space-between;
`
const IsDoneContainerStyled = styled.div`
  flex: 0 0 2.4rem;
`
const CodeContainerStyled = styled.div`
  flex: 0 0 4.1rem;
  font-size: ${FontSizes.CompactCardBig};
  color: ${Colors.black};
`
const NameContainerStyled = styled.div`
  flex: 1 0;
  color: ${Colors.greyMiddle};
`

export const KitchenOrderWareLine: React.FC<IProps> = ({
  recipeId,
  code,
  label,
  isDone,
  isCheckable,
}) => {
  const updateOrderRecipeState = useMKitchenRecipeState()

  return (
    <OrderWareLineWrap>
      <IsDoneContainerStyled>
        {isCheckable && (
          <TouchCheckButton
            isChecked={isDone}
            onClick={() => {
              if (recipeId) {
                updateOrderRecipeState({
                  recipeId,
                  state: isDone
                    ? OrderRecipeStateEnum.Preparing
                    : OrderRecipeStateEnum.Cooked,
                })
              }
            }}
          />
        )}
      </IsDoneContainerStyled>
      <CodeContainerStyled>{code}</CodeContainerStyled>
      <NameContainerStyled>{label}</NameContainerStyled>
    </OrderWareLineWrap>
  )
}
